.DiscountHeader {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin-bottom: 15px;
  margin-top: -15px;
  background-color: #3ab355;
  color: #fff;
  font-weight: 500;
  padding: 7px;
  border-radius: 4px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  font-size: 1.7rem;

  &:hover {
    text-decoration: none;
  }

  a:hover {
    text-decoration: none;
  }

  &__stress {
    display: contents;
    color: #fbff53;
    margin-left: 4px;
    margin-right: 4px;
  }
}


